.backCross {
  max-width: 25px;
  border-radius: 50px;
  z-index: 2;
  position: absolute;
  right: -25px;
  top: 60%;

  padding: 10px;
  -webkit-box-shadow: 0 2px 4px 0 rgba(0,0,0,0.35);
          box-shadow: 0 2px 4px 0 rgba(0,0,0,0.35);
  background-color: #ffffff;
}
.backCross:hover {
  	-webkit-transform: scale(1.04, 1.04);
  	    -ms-transform: scale(1.04, 1.04);
  	        transform: scale(1.04, 1.04);
    -webkit-transform-origin: center;
        -ms-transform-origin: center;
            transform-origin: center;
    -webkit-box-shadow: 0 4px 4px 0 rgba(0,0,0,0.35);
            box-shadow: 0 4px 4px 0 rgba(0,0,0,0.35);
}

.projectInfo {
  position: fixed;
  top: 0;
  padding-top: 80px;
  /* box-shadow: 3px 0 5px -2px rgba(0,0,0,0.35); */
  height: 100vh;
}

.videoWrapper {
	position: relative;
	padding-bottom: 50%;
	/* 16:9 */
	padding-top: 25px;
	height: 0;
  margin-bottom: 30px;
}
.videoWrapper iframe {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
}

.projectImage {
  width: 100%;
  padding: 10px 0;
}

.body {
  height: 100%;
  margin-left: auto;
  margin-right: auto;
}

/* lg */
@media only screen and (min-width: 1440px) {
  .body {
    max-width: 1020px;
  }
}

/* md */
@media only screen and (max-width: 1439px) {
  .body {
    max-width: 876px;
  }
}

/* sm */
@media only screen and (max-width: 1023px) {
  .body {
    width: 768px;
  }
}

/* xs */
@media only screen and (max-width: 767px) {
    .backCross {
        position: fixed;
        top: 90%;
        left: 50%;
        -webkit-transform: translateX(-50%);
            -ms-transform: translateX(-50%);
                transform: translateX(-50%);
    }
    .backCross:hover {
      position: fixed;
      top: 90%;
      left: 50%;
      
      -webkit-transform: scale(1.04, 1.04);
      
          -ms-transform: scale(1.04, 1.04);
      
              transform: scale(1.04, 1.04);
      -webkit-transform: translateX(-50%);
          -ms-transform: translateX(-50%);
              transform: translateX(-50%);
      -webkit-transform-origin: center;
          -ms-transform-origin: center;
              transform-origin: center;
      -webkit-box-shadow: 0 4px 4px 0 rgba(0,0,0,0.35);
              box-shadow: 0 4px 4px 0 rgba(0,0,0,0.35);
    }

    .projectInfo {
        position: absolute;
        top: 0;
        padding-top: 80px;
        -webkit-box-shadow: none;
                box-shadow: none;
        height: auto;
    }

    .projectContent>div>p,h2 {
        position: relative;
        left: 8.3333%;
        width: 83.3333%;
    }
}


